import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorker from './serviceWorker';
//import App js
import App from './App';
//import bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
//import font-awesome
import 'font-awesome/css/font-awesome.css';
